<template>
  <div class="history">
    <van-nav-bar title="" left-text="" right-text="" left-arrow @click-left="$router.back()" />
    <van-row :gutter="20" style="margin-top:20px;">
      <van-col :span="24">
        <van-list :finished="finished" @load="onLoad">
          <van-cell v-for="item in list" :key="item.tradeNo" :value="formatTime(item.createOrderTime) + ''" label="" :title="`${item.phoneNo}`" @click="handleClick(item)" />
        </van-list>
        <div v-if="list.length === 0" style="text-align:center;"> {{noDataMsg}} </div>
      </van-col>
    </van-row>
    <van-dialog v-model="showDialog" confirmButtonText="">
      <van-field v-model="form.fullMerchantsName" label="" />
      <van-field v-model="form.tradeNo" label="" />
      <van-field :value="formatTime(form.createOrderTime)" label="" />
      <van-field :value="form.phoneNo" label="" />
      <van-field :value="`${form.goodsName}`" label="" />
      <van-field :value="`${form.orderAmt}`" label="" />
    </van-dialog>
  </div>
</template>

<script>
import moment from 'moment'
export default {
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      showDialog: false,
      form: {},
      noDataMsg: ''
    }
  },

  methods: {
    onLoad() {},
    handleClick(item) {
      this.form = item
      this.showDialog = true
      // this.$router.push({ name: 'CompletePersonInfo', query: item })
    },
    formatTime(timeStamp) {
      return moment(timeStamp).format('YYYY-MM-DD HH:mm:ss')
    },
    getHistory() {
      const vm = this
      const param = {
        wxId: this.$store.state.userinfo.openid
      }

      vm.$store.commit(vm.$types.ShowLoading, true)
      this.$api.GetPayHistory(param).then(res => {
        if (res.data.code == '200') {
          vm.list = res.data.data
        } else {
          vm.$toast.fail(`${res.data.message}`)
        }
        if (vm.list.length === 0) {
          vm.noDataMsg = ''
        }
        vm.$store.commit(vm.$types.ShowLoading, false)
      })
    }
  },
  created() {
    this.getHistory()
  }
}
</script>



